* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.location-container {
  margin-top: 10rem;
  width: 100%;
  height: 400px;
}

.cardsHolder {
  width: 100%; 
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.cardNum1 {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.cardNum1>img{
  transform: rotate(0deg);
  transition: 0.4s ease-in-out;
}

.cardNum1>img:hover{
  transform: rotate(360deg);
  cursor: pointer;
}
.cardNum1:nth-child(2)>img:hover{
  transform: rotate(-360deg);
  cursor: pointer;
}

.cardNum1 > h1 {
  margin-top: 48px;
  font-family: "Jost", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  text-align: center;
  letter-spacing: 5px;
  color: #333136;
  text-transform: uppercase;
}

#btn-loc {
  margin-top: 32px;
  width: 152px;
  height: 56px;
  background: #e7816b;
  border-radius: 8px;
  font-family: "Jost", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #ffffff;
  outline: none;
  border: none;
  transform: scale(1);
  transition: 0.4s ease-in-out;
  overflow: hidden;
}

#btn-loc:hover{
  background-color: #f3a998;
  transform: scale(1.1);
  cursor: pointer;
  color: black;
}